<template>
  <div class="flex-user">
    <div
      style="
        background-color: #fff;
        margin-bottom: 10px;
        padding: 12px 24px;
        box-sizing: border-box;
        border-radius: 4px;
      "
    >
      <a-form :form="form" layout="vertical">
        <a-row :gutter="24">
          <a-col :span="4">
            <a-form-item label="角色">
              <a-input
                placeholder="请输入角色名 "
                v-model="form.userName"
              ></a-input>
            </a-form-item>
          </a-col>
          <a-col :span="4">
            <a-form-item :label="'\u00a0'">
              <g-btn text="查询"></g-btn>
            </a-form-item>
          </a-col>
        </a-row>
      </a-form>
    </div>

    <div
      style="
        background-color: #fff;
        padding: 12px 24px 0;
        box-sizing: border-box;
        overflow-y: hidden;
        border-radius: 4px;
        display: flex;
        flex-direction: column;
        flex: 1;
      "
    >
      <div
        style="display: flex; justify-content: flex-end; margin-bottom: 10px"
      >
        <g-btn
          text="添加"
          class="mr-1"
          @callback="addRole"
        ></g-btn>
        <g-btn text="批量删除" class="mr-1"></g-btn>
        <g-btn text="下载模板" class="mr-1"></g-btn>
        <g-btn text="导入excel" class="mr-1"></g-btn>
      </div>
      <div>
      <a-table
      style="flex: 1;"
        :row-key="(record) => record.id"
        id="tables"
        :columns="columns"
        :data-source="data"
        :scroll="{ y: tableheight }"
        :pagination="{
          current,
          total,
          pageSize,
        }"
      >
        <!-- 角色 -->
        <span slot="role" slot-scope="text">
          <a-tag color="blue">{{ text }}</a-tag>
        </span>
        <!-- 操作 -->
        <span slot="action" slot-scope="text">
          <a href="javascript:;" class="mr-1">编辑</a>
          <a href="javascript:;" class="del">删除</a>
        </span>
      </a-table>
    </div>
    </div>
  </div>
</template>

<script>
import { getTableScroll } from "@/utils/getTableScroll";
import createModal from "@/utils/modal";
import CreateEditRole from "./create-edit-role/create-edit-role.vue";

export default {
  name: "DataV2User",

  data() {
    return {
      tableheight: 0,
      form: {
        userName: "",
      },
      current: 1,
      total: 0,
      pageSize: 10,
      columns: [
        {
          title: "序号",
          dataIndex: "id",
          key: "id",
          customRender: (text, record, index) => `${index + 1}`,
          width: 80,
        },
        {
          title: "角色",
          dataIndex: "role",
          key: "role",
          scopedSlots: { customRender: "role" },
        },
        {
          title: "用户邮箱",
          dataIndex: "email",
          key: "email",
          ellipsis: true,
        },
        {
          title: "用户名",
          dataIndex: "name",
          key: "name",
        },

        {
          title: "创建时间",
          dataIndex: "createTime",
          key: "createTime",
        },

        {
          title: "操作",
          dataIndex: "action",
          key: "action",
          scopedSlots: { customRender: "action" },
        },
      ],
      data: [
        
      ],
    };
  },

  mounted() {
    this.$nextTick(() => {
      this.tableheight = getTableScroll({ id: "tables" });
      Array.from({ length: 9 }, (_, index) => {
        this.data.push({
          id: index + 11,
          role: "管理员",
          email: "123@qq.com",
          name: "admin",
          createTime: "2022-01-01 00:00:00",  
        });
      });
      this.total = this.data.length;
    });
  },

  methods: {
    addRole() {
      createModal(
        { title: "添加角色", width: "800px" },
        { component: CreateEditRole }
      );
    },
  },
};
</script>

<style lang="scss" scoped>
.flex-user {
  box-sizing: border-box;
  height: calc(100% - 12px);
  display: flex;
  flex-direction: column;
}
</style>
